<template>
  <div class="user-settings">
    <h2> </h2>

    <div v-if="!userStore.isLoaded">
         ...
    </div>

    <div v-else-if="userStore.users.length>0">
      <div class="user-info" v-for="user in userStore.users" :key="user.id">
        <img :src="user.image || defaultAvatar" alt="User Avatar" class="avatar" />
        <p><strong> :</strong> {{ user.fullName }}</p>
        <p><strong>:</strong> {{ user.mobile }}</p>
        <p><strong>  :</strong> {{ user.wallet }} </p>
        <p><strong> :</strong> {{ user.tokenScore }}</p>
        <p><strong>  :</strong> {{ user.finger ? '' : '' }}</p>
        <p><strong>:</strong> {{ user.rule ? '' : '' }}</p>
      </div>
      <div class="actions">
        <button @click="refreshUser"> </button>
      </div>
    </div>
    <div class="error" v-else>
          
    </div>
  </div>
</template>
<script setup>
    import { useUserStore } from '@/stores/user'
    import { onMounted, ref } from 'vue'
    const userStore = useUserStore()
    const defaultAvatar = ref('/images/default-avatar.png')
    const refreshUser = () => {
      userStore.fetchUserInfo()
    }
    onMounted(() => {
        userStore.fetchUserInfo()
    })
</script>